const CustomersComponent = ({ items }) => {
  return (
    <section className="py-5">
      <div className="container py-5">
        <p className="h6 mb-1 text-uppercase text-primary mb-3"></p>
        <div className="row mb-5">
          <div className="col-lg-5">
            <h2 className="mb-5"></h2>
          </div>
          <div className="col-lg-7">
            <p className="text-muted">
              ,
              
            </p>
          </div>
        </div>
        <div className="owl-carousel customers-slide">
          {items.map((item, index) => {
            return (
              <div key={index} className="testimonial p-5 shadow-sm">
                <div className="d-flex align-items-center mb-1">
                  <div>
                    {/* <div
                      className="img-fluid"
                      style={{ background: `url(${item.pic})` }}
                    /> */}
                    <img
                      style={{ height: "180px" }}
                      className="img-fluid "
                      src={item.pic}
                      alt=""
                    />
                  </div>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </section>
  );
};
export default CustomersComponent;
